import { ReactElement } from "react"
import { WatchlistSuggestion } from "@rp/react/common/watchlists"
import { Typography } from "@rp/react/web/mui/core"
import {
  ArticleOutlined,
  AttachMoneyOutlined,
  CompareArrowsOutlined,
  CurrencyExchangeOutlined,
  FlashOn,
  GavelOutlined,
  GradeOutlined,
  GroupWorkOutlined,
  Insights,
  LeaderboardOutlined,
  LegendToggleOutlined,
  MergeTypeOutlined,
  MonetizationOnOutlined,
  NewReleasesOutlined,
  PeopleOutlined,
  PublicOutlined,
  ReportGmailerrorred,
  ReportProblemOutlined,
  RocketLaunchOutlined,
  SentimentSatisfiedAlt,
  ShowChartOutlined,
  SsidChart,
  StarOutline,
  TimelineOutlined,
  TravelExploreOutlined,
  TrendingUp,
  TrendingUpOutlined,
  VpnKeyOutlined,
  WebOutlined,
  WorkOutlineOutlined,
} from "@rp/react/web/mui/icons"

type Props = {
  suggestion: WatchlistSuggestion
  onSuggestionClick: (suggestion: WatchlistSuggestion) => void
}

const getIcon = (suggestion: WatchlistSuggestion): ReactElement | null => {
  switch (suggestion.iconName) {
    case "ArticleOutlined":
      return <ArticleOutlined />
    case "TrendingUpOutlined":
      return <TrendingUpOutlined />
    case "ReportGmailerrorred":
      return <ReportGmailerrorred />
    case "ShowChartOutlined":
      return <ShowChartOutlined />
    case "PublicOutlined":
      return <PublicOutlined />
    case "GavelOutlined":
      return <GavelOutlined />
    case "MergeTypeOutlined":
      return <MergeTypeOutlined />
    case "WebOutlined":
      return <WebOutlined />
    case "VpnKeyOutlined":
      return <VpnKeyOutlined />
    case "NewReleasesOutlined":
      return <NewReleasesOutlined />
    case "RocketLaunchOutlined":
      return <RocketLaunchOutlined />
    case "GradeOutlined":
      return <GradeOutlined />
    case "LeaderboardOutlined":
      return <LeaderboardOutlined />
    case "PeopleOutlined":
      return <PeopleOutlined />
    case "ReportProblemOutlined":
      return <ReportProblemOutlined />
    case "TravelExploreOutlined":
      return <TravelExploreOutlined />
    case "CompareArrowsOutlined":
      return <CompareArrowsOutlined />
    case "TrendingUp":
      return <TrendingUp />
    case "SsidChart":
      return <SsidChart />
    case "Insights":
      return <Insights />
    case "WorkOutlineOutlined":
      return <WorkOutlineOutlined />
    case "AttachMoneyOutlined":
      return <AttachMoneyOutlined />
    case "GroupWorkOutlined":
      return <GroupWorkOutlined />
    case "FlashOn":
      return <FlashOn />
    case "StarOutline":
      return <StarOutline />
    case "TimelineOutlined":
      return <TimelineOutlined />
    case "CurrencyExchangeOutlined":
      return <CurrencyExchangeOutlined />
    case "MonetizationOnOutlined":
      return <MonetizationOnOutlined />
    case "LegendToggleOutlined":
      return <LegendToggleOutlined />
    case "SentimentSatisfiedAlt":
      return <SentimentSatisfiedAlt />
    default:
      return null
  }
}

export const WatchlistChatBotSuggestion = ({ suggestion, onSuggestionClick }: Props) => {
  return (
    <Typography
      key={suggestion.id}
      onClick={() => onSuggestionClick(suggestion)}
      sx={theme => ({
        fontSize: 18,
        letterSpacing: "-0.25px",
        py: 0.3,
        pl: 1,
        pr: 2,
        my: 1,
        border: `1px solid transparent`,
        backgroundColor: theme.palette.elevation.dp24,
        borderRadius: "24px",
        width: "fit-content",
        display: "flex",
        alignItems: "center",
        gap: 1,
        "& svg": {
          fontSize: 20,
          color: "grey.300",
        },
        "&:hover": {
          cursor: "pointer",
          border: `1px solid ${theme.palette.outline.border}`,
        },
      })}
    >
      {getIcon(suggestion)}
      {suggestion.label}
    </Typography>
  )
}
